@use '../util' as *;

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  font-family: var(--font-text);
  margin: 0;
  padding: 0;
  // background-color: red;
}

// SPACING
.container {
  // width: 80%;
  // max-width: rem(1200);
  // margin: 0 auto;
  width: 80%;
  width: min(90%, em(1200));
  margin-inline: auto;

  &__content {
    width: 60%;
    margin-bottom: rem(40);

    @include breakpoint-down(medium) {
      width: 80%;
    }

    @include breakpoint-down(small) {
      width: 100%;
    }
  }
}

.row {
  padding: 0 rem(20);
}

// Flexbox
.flex {
  display: flex;

  &-jc-sb {
    justify-content: space-between;
  }

  &-jc-sa {
    justify-content: space-around;
  }

  &-jc-c {
    justify-content: center;
  }

  &-jc-fs {
    justify-content: flex-start;
  }

  &-jc-fe {
    justify-content: flex-end;
  }

  &-ai-fs {
    align-items: flex-start;
  }

  &-ai-c {
    align-items: center;
  }

  &-fd-r {
    flex-direction: row;
  }

  &-fd-c {
    flex-direction: column;
  }

  &-fw-w {
    flex-wrap: wrap;
  }

  &-fw-nw {
    flex-wrap: nowrap;
  }
}